/*
  Link
  Default link styles
*/
a{
  color: getThemeProperty(primaryColor);
  text-decoration: underline;
  &:link{
    color: getThemeProperty(primaryColor);
  }
  &:visited{
    // Does not work with CSS vars
    // color: darken(getThemeProperty(primaryColor), 10);
    filter: brightness(.5);
  }
  &:hover{
    text-decoration: none;
  }
  &:active{
    // Does not work with CSS vars
    // color: lighten(getThemeProperty(primaryColor), 10);
    filter: brightness(2);
  }
  @include focus-style;
}
